import React from 'react';
import styled from 'styled-components';

const SidebarOpContainer = styled.div`
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 10px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    cursor: pointer;
    color: #818181;
    >svg {
        font-size: 1.2rem;
    }
    &.sideOption-active > p {
        visibility: visible;
        font-weight: 800;
    }
    &.sideOption-active,
    &.sideOption-active > p,
    &.sideOption-active > h3,
    &:hover,
    &:hover > p,
    &:hover > h3 {
        visibility: visible;
        color: #c04b37;
        background-color: #fcecec;
        font-weight: 800;
    }
`
const H3 = styled.h3`
    flex: 1;
    margin-left: 10px;
    font-size: 0.9rem;
    font-weight: 400;
`
const P = styled.p`
    visibility: hidden;
    font-weight: 300;
`


export default function SidebarOption({ Icon, title, number, selected }) {
    return (
        <SidebarOpContainer className={selected && 'sideOption-active'}>
            <Icon />
            <H3>{title}</H3>
            <P>{number}</P>
        </SidebarOpContainer>
    )
}
