import { IconButton } from '@material-ui/core';
import React from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import MoveToInboxIcon from '@material-ui/icons/MoveToInbox';
import ErrorIcon from '@material-ui/icons/Error';
import DeleteIcon from '@material-ui/icons/Delete';
import EmailIcon from '@material-ui/icons/Email';
import WatchLaterIcon from '@material-ui/icons/WatchLater';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import LabelImportantIcon from '@material-ui/icons/LabelImportant';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import UnfoldMoreIcon from '@material-ui/icons/UnfoldMore';
import PrintIcon from '@material-ui/icons/Print';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { useSelector } from 'react-redux';
import { selectOpenMail } from '../features/mailSlice';

const MailConatiner = styled.article`
    flex: 1;
    background-color: whitesmoke;
`
const MailTools = styled.div`
    display: flex;
    justify-content: space-between;
    background-color: white;
`
const ToolsLeft = styled.div`
    display: flex;
`
const ToolsRight = styled.div`

`
const MailBody = styled.div`
    display: flex;
    flex-direction: column;
    margin: 30px;
    background-color: white;
    padding: 20px;
    height: 100%;
    box-shadow: 0 5px 7px 0 rgba(0,0,0,0.24);
`
const MailBodyHeader = styled.div`
    display: flex;
    align-items: center;
    padding: 20px;
    position: relative;
    >svg {
        color: #e8ab02;
    }
`
const H2 = styled.h2`
    font-weight: 400;
    margin-right: 20px;
`
const P = styled.p`
    margin-left: 20px;
`
const Span = styled.span`
    position: absolute;
    top: 24px;
    right: 0;
    font-size: 0.8rem;
    color: grey;
`
const MailMessage = styled.div`

`
const Text = styled.p`
    padding: 10px;
    margin-right: 20px;
    overflow-wrap: break-word;
`

export default function Mail() {
    const history = useHistory();

    const selectedMail = useSelector(selectOpenMail);

    return (
        <MailConatiner>
            <MailTools>
                <ToolsLeft>
                    <IconButton onClick={() => history.push("/")}>
                        <ArrowBackIcon />
                    </IconButton>

                    <IconButton>
                        <MoveToInboxIcon />
                    </IconButton>

                    <IconButton>
                        <ErrorIcon />
                    </IconButton>

                    <IconButton>
                        <DeleteIcon />
                    </IconButton>

                    <IconButton>
                        <EmailIcon />
                    </IconButton>

                    <IconButton>
                        <WatchLaterIcon />
                    </IconButton>

                    <IconButton>
                        <CheckCircleIcon />
                    </IconButton>

                    <IconButton>
                        <LabelImportantIcon />
                    </IconButton>

                    <IconButton>
                        <MoreVertIcon />
                    </IconButton>

                </ToolsLeft>

                <ToolsRight>
                    <IconButton>
                        <UnfoldMoreIcon />
                    </IconButton>

                    <IconButton>
                        <PrintIcon />
                    </IconButton>

                    <IconButton>
                        <ExitToAppIcon />
                    </IconButton>
                </ToolsRight>
            </MailTools>

            <MailBody>
                <MailBodyHeader>
                    <H2>{selectedMail?.subject}</H2>
                    <LabelImportantIcon />
                    <P>{selectedMail?.title}</P>
                    <Span>{selectedMail?.time}</Span>
                </MailBodyHeader>

                <MailMessage>
                    <Text>{selectedMail?.description}</Text>
                </MailMessage>
            </MailBody>
        </MailConatiner>
    )
}
