import { Button } from '@material-ui/core';
import React from 'react';
import styled from 'styled-components';
import AddIcon from '@material-ui/icons/Add';
import InboxIcon from '@material-ui/icons/Inbox';
import SidebarOption from './SidebarOption';
import StarIcon from '@material-ui/icons/Star';
import LabelImportantIcon from '@material-ui/icons/LabelImportant';
import NearMeIcon from '@material-ui/icons/NearMe';
import NoteIcon from '@material-ui/icons/Note';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import { IconButton } from '@material-ui/core';
import PersonIcon from '@material-ui/icons/Person';
import DuoIcon from '@material-ui/icons/Duo';
import PhoneIcon from '@material-ui/icons/Phone';
import { useDispatch } from 'react-redux';
import { openSendMessage } from '../features/mailSlice';


const SidebarContainer = styled.aside`
    flex: 0.3;
    max-width: 300px;
    padding-right: 20px;
`
const SidebarFooter = styled.div`
    
`
const FooterIcons = styled.div`
    display: flex;
    justify-content: center;
`
export default function Sidebar() {
    const dispatch = useDispatch();


    return (
        <SidebarContainer>
            <Button
                startIcon={<AddIcon />}
                onClick={() => dispatch(openSendMessage())}
                style={{ marginTop: 15, marginBottom: 15, borderRadius: 30, padding: 15, boxShadow: '0 2px 5px -2px rgba(0,0,0,0.75)' }}
            >
                Compose
            </Button>
            <SidebarOption Icon={InboxIcon} title="Inbox" number={54} selected={true} />
            <SidebarOption Icon={StarIcon} title="Starred" number={23} />
            <SidebarOption Icon={AccessTimeIcon} title="Snoozed" number={3} />
            <SidebarOption Icon={LabelImportantIcon} title="Important" number={10} />
            <SidebarOption Icon={NearMeIcon} title="Sent" number={2} />
            <SidebarOption Icon={NoteIcon} title="Drafts" number={5} />
            <SidebarOption Icon={ExpandMoreIcon} title="More" number={3} />

            <SidebarFooter>
                <FooterIcons>
                    <IconButton>
                        <PersonIcon />
                    </IconButton>
                    <IconButton>
                        <DuoIcon />
                    </IconButton>
                    <IconButton>
                        <PhoneIcon />
                    </IconButton>
                </FooterIcons>
            </SidebarFooter>
        </SidebarContainer>
    )
}
