import styled from 'styled-components';
import { Checkbox, IconButton } from '@material-ui/core';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import RedoIcon from '@material-ui/icons/Redo';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import React, { useState } from 'react';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import KeyboardHideIcon from '@material-ui/icons/KeyboardHide';
import SettingsIcon from '@material-ui/icons/Settings';
import Section from './Section';
import InboxIcon from '@material-ui/icons/Inbox';
import PeopleIcon from '@material-ui/icons/People';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import EmailRow from './EmailRow';
import { useEffect } from 'react';
import { db } from './firebase';

const EmailListConatiner = styled.main`
    flex: 1;
    overflow: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
`
const EmailListSettings = styled.div`
    display: flex;
    border-bottom: 1px solid whitesmoke;
    background-color: white;
    justify-content: space-between;
    z-index: 69;
    padding-right: 10px;
`
const LeftSide = styled.div`

`
const RightSide = styled.div`

`
const EmailListSections = styled.div`
    position: sticky;
    top: 0;
    display: flex;
    background-color: white;
    border-bottom: 1px solid whitesmoke;
    z-index: 60;
`
const List = styled.div`
    padding-bottom: 10%;
`

export default function EmailList() {
    const [emails, setEmails] = useState([]);

    useEffect(() => {
        return db.collection('emails').orderBy('timestamp', 'desc').onSnapshot(snap => setEmails(snap.docs.map(doc => ({
            id: doc.id,
            data: doc.data()
        }))))
    }, [])


    return (
        <EmailListConatiner>
            <EmailListSettings>
                <LeftSide>
                    <Checkbox color="secondary" />
                    <IconButton>
                        <ArrowDropDownIcon />
                    </IconButton>
                    <IconButton>
                        <RedoIcon />
                    </IconButton>
                    <IconButton>
                        <MoreVertIcon />
                    </IconButton>
                </LeftSide>

                <RightSide>
                    <IconButton>
                        <ChevronLeftIcon />
                    </IconButton>
                    <IconButton>
                        <ChevronRightIcon />
                    </IconButton>
                    <IconButton>
                        <KeyboardHideIcon />
                    </IconButton>
                    <IconButton>
                        <SettingsIcon />
                    </IconButton>
                </RightSide>
            </EmailListSettings>

            <EmailListSections>
                <Section Icon={InboxIcon} title="primary" color="red" selected />
                <Section Icon={PeopleIcon} title="Social" color="#1A73E8" />
                <Section Icon={LocalOfferIcon} title="Promotions" color="green" />
            </EmailListSections>

            <List>
                {emails.map(({ id, data: { to, subject, message, timestamp } }) => (
                    <EmailRow id={id} key={id} title={to} subject={subject} description={message} time={new Date(timestamp?.seconds * 1000).toUTCString()} />
                ))}
            </List>
        </EmailListConatiner>
    )
}
