import React from 'react';
import styled from 'styled-components';

const SectionContainer = styled.section`
    display: flex;
    align-items: center;
    cursor: pointer;
    min-width: 200px;
    padding:  0 15px;
    border-bottom-width: 2px;
    border-width: 0;
    color: grey;
    &:hover {
        background-color: whitesmoke;
        border-width: 3px;
    }
    &.selection-selected {
        background-color: whitesmoke;
        border-width: 3px;
    }

`
const H4 = styled.h4`
    margin-left: 15px;
    font-size: 0.9rem;
`


export default function Section({ Icon, title, color, selected }) {
    return (
        <SectionContainer className={selected && 'selection-selected'} style={{
            borderBottom: `3px solid ${color}`,
            color: `${selected && color}`,
        }}>
            <Icon />
            <H4>{title}</H4>
        </SectionContainer>
    )
}
