import React from 'react';
import styled from 'styled-components';
import CloseIcon from '@material-ui/icons/Close';
import { Button } from '@material-ui/core';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { closeSendMessage } from '../features/mailSlice';
import { db } from './firebase';
import firebase from 'firebase';


const SendMailContainer = styled.section`
    position: absolute;
    bottom: 0;
    right: 50px;
    background-color: #404040;
    width: 40%;
    height: 40%;
    max-width: 500px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    flex-direction: column;
    border: 1px solid whitesmoke;
    box-shadow: 0 5px 7px 0 rgba(0,0,0,0.24);
`
const Header = styled.div`
    padding: 13px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: gray;
    >svg {
        cursor: pointer;
    }
`
const Form = styled.form`
    display: flex;
    flex: 1;
    flex-direction: column;
`
const Input = styled.input`
    height: 30px;
    padding: 10px;
    border: none;
    border-bottom: 1px solid whitesmoke;
`
const H3 = styled.h3`
    color: whitesmoke;
    font-size: 0.8rem;
`
const SendBtnContainer = styled.div`
    padding: 15px;
`
const P = styled.p`
    background-color: white;
    color: #ff7e7e;
    font-weight: 700;
    margin: 0;
    text-align: right;
`

export default function SendMail() {
    const { register, handleSubmit, formState: { errors } } = useForm();

    const onSubmit = (formData) => {
        db.collection('emails').add({
            to: formData.to,
            subject: formData.subject,
            message: formData.message,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        });
        dispatch(closeSendMessage());
    }

    const dispatch = useDispatch();


    return (
        <SendMailContainer>
            <Header>
                <H3>New message</H3>
                <CloseIcon onClick={() => dispatch(closeSendMessage())} />
            </Header>

            <Form onSubmit={handleSubmit(onSubmit)} >
                <Input name="to" type="email" placeholder="To" {...register("to", { required: true })} />
                {errors.to && <P>"To" is required</P>}
                <Input name="subject" type="text" placeholder="Subject" {...register("subject", { required: true })} />
                {errors.subject && <P>"Subject" is required</P>}
                <Input style={{ flex: 1 }} name="message" type="text" placeholder="Message..." {...register("message", { required: true })} />
                {errors.message && <P>"Message" is required</P>}

                <SendBtnContainer>
                    <Button style={{ background: "deepskyblue", color: "white" }} variant="contained" type="submit">Send</Button>
                </SendBtnContainer>
            </Form>
        </SendMailContainer>
    )
}
